body, html, #root, .app, .container{
  width: 100%;
  height: 100%;

}

.container {
font-size:28px;
color: #fff;
display:flex;
flex-direction: column;
align-items: center;
justify-content: center;
background-color: black;
}

.loginform {
  box-sizing: border-box;
  width: 100%;
  max-width:350px;
  padding-left: 20px;
  padding-right: 20px;
 
}

.inputField {
  padding-top: 16px;
}

.input {
  width:280px;
  height:40px;
  align-items: center;
}

.btn{
  
  color:white;
  background:grey;
  height:40px;
  width:120px;
  border-radius: 8px;
  margin-top:2vh;
}
